import newId from '../utils/newid'

export default function lists(state = {}, action) {
    switch (action.type) {
        case 'ADD_LIST':
            let newList = {};
            let id = newId();

            newList[id] = {tasksIds: []};

            return Object.assign({}, state, newList);
        case 'DELETE_LIST':
            let newState = Object.assign({}, state);
            delete newState[action.id];

            //TODO remove tasks

            return newState;
        default:
            return state
    }
}
